* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  line-height: 1.4;
  font-family: 'futura';
}

img {
  margin-top: 10px;
  margin-bottom: 10px;
}

p {
  margin-right: 70px;
  font-size: 18px;
  margin-top: 15px;
}

h4 {
  margin-top: 20px;
}

h3 {
  font-weight: bold;
  margin-top: 25px;
  margin-bottom: 10px;
}

.text-center {
  text-align: center;
}

.blog-title {
  text-align: center;
  padding-bottom: 10px;
  padding-top: 15px;
  padding-left: 7px;
  font-weight: 600;
  line-height: 1.6;
}

#about-serverless-guru {
  font-size: 24px;
  line-height: 2;
  padding-top: 20px;
  padding-left: 20px;
  text-align: center;
}

.main-nav {
  padding-top: 4px;
  padding-right: 30px;
  cursor: pointer;
  color: #000;
  font-size: 18px;
}

.main-nav-active {
  color: #C13725;
}

.flex {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

a:hover {
  text-decoration: none;
}

img.logo {
  width: 105px;
}

img.blog {
  margin-top: 25px;
  display: block;
  width: 100%;
  height: 80%;
  max-height: 600px;
}

img.profile {
  width: 300px;
  height: 300px;
  margin-left: 130px;
  margin-bottom: 20px;
  margin-top: 45px;
}

header {
  height: 120px;
  border-bottom: 2px solid black;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-bottom: 5px;
  display: flex;
  justify-content: space-between;
}

.header-serverless-guru {
  font-weight: 600;
}

iframe {
  border: 0;
}

.profile-name {
  text-align: center;
  padding-left: 130px;
}

.profile-title {
  text-align: center;
  padding-left: 130px;
  padding-bottom: 20px;
}

.vids {
  margin-top: 50px;
  margin-bottom: 50px;
}

